.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.main {
  display: flex;
  height: 100vh;
  /* height: calc(100vh - env(safe-area-inset-bottom, 100px) - env(safe-area-inset-top, 100px)); */
}

.container {
  width: 100vw;
  background-color: blue;
  border: solid 0.125rem lightgray;
  margin-top: calc(0.1rem + env(safe-area-inset-top, 0px));
  margin-right: calc(0.1rem + env(safe-area-inset-right, 0px));
  margin-bottom: calc(0.1rem + env(safe-area-inset-bottom, 0px));
  margin-left: calc(0.1rem + env(safe-area-inset-left, 0px));
}

:root {
  --sat: env(safe-area-inset-top);
  --sar: env(safe-area-inset-right);
  --sab: env(safe-area-inset-bottom, 42px);
  --sal: env(safe-area-inset-left);
}